html{box-sizing:border-box;height:100%}*,:after,:before{box-sizing:inherit}body{display:flex;align-items:center;justify-content:center;min-height:100%;margin:0;padding:0;background:#36d1dc;background:-webkit-linear-gradient(90deg,#5b86e5,#36d1dc);background:linear-gradient(90deg,#5b86e5,#36d1dc);font-family:Helvetica Neue,Helvetica,Arial,sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}.content{text-align:center;transform:translateY(-5%)}.avatar{display:inline-block;border-radius:50%;border:4px solid #fff;transition:transform .5s ease-in-out;box-shadow:0 0 10px 2px rgba(0,0,0,.2);background-image:url(https://www.gravatar.com/avatar/52869e2103531ad458ab57bf53b29679?s=280);background-size:cover;width:140px;height:140px}.avatar img{display:none}.avatar:active{transform:rotate(1turn)}.title h2{font-size:28px;font-weight:200;letter-spacing:2px;color:#fff;margin:30px 0 15px}.social-icons{flex-wrap:wrap}.social-icons,.social-icons a{display:flex;align-items:center;justify-content:center}.social-icons a{background-color:hsla(0,0%,100%,.1);border-radius:50%;width:60px;height:60px;margin:5px;text-decoration:none;color:#fff;transition:background-color .3s}.social-icons a:active{background-color:hsla(0,0%,100%,.2)}.social-icons a i{font-size:34px}.social-icons a i.icon-medium{font-size:30px}.social-icons a i.icon-camera,.social-icons a i.icon-tumblr{font-size:28px}.social-icons a i.icon-soundcloud{font-size:30px}@media only screen and (min-width:961px){.avatar:hover{transform:rotate(1turn)}.social-icons a:hover{background-color:hsla(0,0%,100%,.2)}}